 /*login*/
 .log {
    position: fixed;      
    top: 0;
    right: 0;
    width: 300px;
    height: 350px;
    padding: 40px 20px 30px 20px;
    border-radius: 12px;
    z-index: 2;

    background-color: var(--herotitle);
    text-align: center; 
}  

.log.is-hidden {
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
    }

.log__btn {
        position: absolute;
        top: 10px;
        right: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        margin: 0;   
        width: 30px;
        height: 30px;
        background-color: transparent;
        border: 1px solid var(--accent-bc);     
        border-radius: 50%; 
        cursor: pointer;
    }
.log__icon {
        fill: var(--accent-bc);

        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        transition-duration: 250ms;
    }
.log__icon:hover,
.log__icon:focus {
        fill: #010320;
    }
.log__form {
        padding: 40px;         
        margin-left: auto;
        margin-right: auto;
    }
.log__text {
        color: var(--accent-bc);    
        font-size: 20px;
        font-weight: 700;
        line-height: 1.1;
        text-align: center; 
    }

.field {
    text-align: left;
    margin-top: 20px;
}
.field__user {
        border-color: var(--accent-bc);
        border-radius: 4px;
        width: 100%;
        height: 30px;
        margin-top: 5px;
    }

.submit-btn {
    padding-top: 11px;
    padding-bottom: 11px;
    padding-left: 43px;
    padding-right: 43px;
    margin-top: 20px;
    border: transparent;
    border-radius: 5px;
    background-color: var(--accent-bc);   
}
.site-nav__link {
    font-size: 15px;
    font-weight: 500;
    line-height: 1.5;
    text-align: center;
    color: var(--herotitle);  
}
