    
    /*hero*/
    .hero-section {
        position: relative;  
        margin-left: auto; 
        margin-right: auto;
        overflow: hidden;
    
        height: 720px;

       text-align: center;
        background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
        url('../images/img.png/hero11.jpg');
    background-color: var(--bgr-color);
      }
    .hero-container {
        &__title {       
          margin-top: 0px;
          margin-left: auto;
          margin-right: auto;
      
          color: var(--herotitle);
          font-weight: 900;    
          line-height: 1.36;
          letter-spacing: 0.06em;
          text-transform: uppercase;
          text-align: center;
        }      
        &__button {        
         border: transparent;
          border-radius: 5px;
          background-color: var(--accent-bc);
      
          font-size: 15px;
          font-weight: 500;
          line-height: 1.5;
          text-align: center;
          color: var(--herotitle);     
        }
      }
      
     
      @media screen and (min-width: 320px) {
        .hero-container {
          width: 100%;
          height: 360px;
          padding-top: 67px;          
        
        &__title {
          margin-bottom: 160px;  
        
          font-size: 24px;    
        }
        &__button {
          padding-top: 10px;
          padding-bottom: 10px;
          padding-left: 15px;
          padding-right: 15px;
          margin-right: 7px;
          font-size: 12px;
        }
      }
    }
      @media screen and (min-width: 768px) {
        .hero-container {
          width: 100%;
          height: 540px;
          padding-top: 100px;       
        
       &__title {
          margin-bottom: 240px;
         
          font-size: 36px;
      }
      &__button {
          padding-top: 15px;
          padding-bottom: 15px;
          padding-left: 33px;
          padding-right: 33px;
          margin-right: 12px;

          font-size: 15px;
          font-weight: 500;
          line-height: 1.5;
      }
      }
    }
      @media screen and (min-width: 1400px) {
      .hero-container {      
          padding-top: 135px;  

          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          text-align: center;         
       
      &__title {
        margin-bottom: 320px;
          
        font-size: 44px;
      }
      &__button {
        padding-top: 15px;
        padding-bottom: 15px;
        padding-left: 34px;
        padding-right: 34px; 
        
        font-size: 15px;
        font-weight: 500;
        line-height: 1.5;
     
       }     
 
    }
  }

