.container {
    margin-left: 30px;
    margin-right: 30px;
     
    
        // mini-mobile sreen from 320px
        @media screen and (min-width: 320px) {
            width: 320px;
            padding-top: 10px;
                }
                // tablet sreen from 768px
                @media screen and (min-width: 768px) {   
            width: 768px;     
            padding-top: 40px;
                }
                // desktop sreen from 1280px
                @media screen and (min-width: 1400px){
            width: 1400px;
            padding-top: 40px;
                }
}
